.jumbotron {
    background: #864aff;
}

.jumbotron > h1 {
    font-family: 'Archivo', sans-serif !important;
    font-weight: 600;
    font-size: 65px;
    color: #00fdcb;
    letter-spacing: 2px;
}

.jumbotron > p {
    font-family: 'Open Sans', sans-serif !important;
    font-weight: 300;
    font-size: 20px;
    color: #ffffff;
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: #2A77AE;
  margin-bottom: 15px;
}

img {
  margin-bottom: 25px;
}


* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

